: 1 0 auto;\n            width: calc(100% / 2);\n}\n}\n@media (max-width: 700px) {\n.card-media[data-v-043ce31e] {\n            flex: 0 0 auto;\n            width: 100%;\n}\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/organizationApplications.vue"],"names":[],"mappings":";AAkFI;QACI,eAAe;QACf,cAAc;QACd,gBAAgB;QAChB,gBAAgB;AACpB;AACA;QACI,aAAa;QACb,8BAA8B;QAC9B,gBAAgB;AACpB;AACA;;QAEI,eAAe;QACf,YAAY;QACZ,YAAY;QACZ,cAAc;AAClB;AACA;QACI,UAAU;AACd;AACA;QACI,gBAAgB;AACpB;AACA;;QAEI,aAAa;QACb,mBAAmB;QACnB,uBAAuB;AAC3B;AACA;QACI,kBAAkB;QAClB,gBAAgB;AACpB;AACA;QACI,mBAAmB;QACnB,cAAc;QACd,uBAAuB;QACvB,kBAAkB;QAClB,mBAAmB;QACnB,eAAe;QACf,mBAAmB;AACvB;AACA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,qBAAqB;AACzB;AACJ;AAEA;AACI;YACI,cAAc;YACd,WAAW;AACf;AACJ","sourcesContent":["<script setup lang=\"ts\">\n    import { computed, ref } from \"vue\";\n    import { container } from \"../../../domain/core/diContainer\";\n    import { IApplication } from \"../../../domain/model/models\";\n    import { IViewModel } from \"../../../domain/viewModel/IViewModel\";\n    import ApplicationCard from \"./application/appCard.vue\"; // resolvable by vue transpiler (bundled)\n    import AppDataTable from \"./application/appDataTable.vue\";\n\n    interface IOrgAppsProps {\n        apps: Array<IApplication>;\n        hideOrgName?: boolean,\n    }\n\n    const props = defineProps<IOrgAppsProps>();\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n\n    let viewData = ref(\"Card\");\n    const selectedOrganizationName = computed(() => viewModel.user!.selectedOrganization?.name);\n    const selectedOrganizationId = computed(() => viewModel.user!.selectedOrganization?.id);\n    // const mainOrgId = computed(() => viewModel.user!.selectedOrganizationGroup.organizationGroup.mainOrganizationId);\n    const orgApps = computed(() => props.apps.sort((a, b) =>\n                        a.createDate && b.createDate ? (new Date(a.createDate).getTime() - new Date(b.createDate).getTime() > 0 ? -1 : 1) : 1\n                    ))\n    \n    function changeView(view: any) {\n        viewData.value = view;\n        let tableVButton = document.getElementById(\"TableView\");\n        let cardVButton = document.getElementById(\"CardView\");\n        if (viewData.value == \"Table\") {\n            cardVButton?.classList.remove(\"activeView\");\n            tableVButton?.classList.add(\"activeView\");\n        } else if (viewData.value == \"Card\") {\n            tableVButton?.classList.remove(\"activeView\");\n            cardVButton?.classList.add(\"activeView\");\n        }\n    }\n</script>\n<template>\n    <div>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"filter-text-container\">\n            <span class=\"applications-text\">{{props.hideOrgName ? '' : selectedOrganizationName }} Applications</span>\n            <div class=\"card-filters\">\n                <i class=\"mdi mdi-view-dashboard dashboard-icon activeView\" id=\"CardView\" @click=\"changeView('Card')\"></i>\n                <i class=\"mdi mdi-format-list-bulleted bulleted-icon\" id=\"TableView\" @click=\"changeView('Table')\"></i>\n            </div>\n        </div>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length > 0\" class=\"cards\">\n            <div v-if=\"props.apps.length == 0\" class=\"empty-org\">\n                <div style=\"justify-content: center; display: grid; margin-bottom: 36px;\">\n                    <img style=\" margin: 36px auto 12px;\" src=\"../../../images/ghost.svg\" />\n                    <span style=\"font-size: 18px; color: #BCBCBC\">It's been empty for a while.</span>\n                </div>\n            </div>\n            <div v-else class=\"row relative-for-children\">\n                <template v-if=\"viewData == 'Card'\">\n                    <div class=\"card-media\" style=\"padding: 0px\" v-for=\"app in orgApps\" :key=\"app.ID\" v-bind:value=\"app.ID\">\n                        <ApplicationCard :app=\"app\" />\n                    </div>\n                </template>\n                <template v-if=\"viewData == 'Table'\">\n                    <AppDataTable />\n                </template>\n            </div>\n        </div>\n        <div v-if=\"viewModel.user?.organizations && viewModel.user?.organizations.length == 0\">\n            <div class=\"need-organization\">\n                <span>To create an application, you must be in an organization.</span>\n            </div>\n            <span class=\"applications-text\">Your Applications</span>\n            <div class=\"create-org-div\">\n                <div style=\"justify-content: center; display: grid; margin-bottom: 36px;\">\n                    <img style=\" margin: 36px auto 12px;\" src=\"../../../images/ghost.svg\" />\n                    <span style=\"font-size: 22px; color: #BCBCBC\">It's been empty for a while.</span>\n                </div>\n                <!-- <button class=\"create-organization-button-appList\" @click=\"openCreateOrganizationDialog\"><i class=\"mdi mdi-plus\" style=\"font-size: 20px; margin-right: 4px;\"></i>CREATE ORGANIZATION</button> -->\n            </div>\n        </div>\n    </div>\n</template>\n\n\n<style scoped>\n    .applications-text {\n        font-size: 22px;\n        color: #151233;\n        font-weight: 500;\n        margin-left: 7px;\n    }\n    .filter-text-container {\n        display: flex;\n        justify-content: space-between;\n        margin-top: 35px;\n    }\n    .dashboard-icon,\n    .bulleted-icon {\n        font-size: 30px;\n        margin: 10px;\n        opacity: 0.5;\n        color: #151233;\n    }\n    .activeView {\n        opacity: 1;\n    }\n    .cards {\n        margin-top: 10px;\n    }\n    .create-org-div,\n    .empty-org {\n        display: grid;\n        align-items: center;\n        justify-content: center;\n    }\n    .relative-for-children {\n        position: relative;\n        margin-left: 0px;\n    }\n    .need-organization {\n        background: #FFFFFF;\n        color: #BCBCBC;\n        align-items: flex-start;\n        padding: 20px 24px;\n        border-radius: 10px;\n        font-size: 22px;\n        margin-bottom: 32px;\n    }\n    @media (min-width: 2400px) {\n        .card-media {\n            flex: 0 0 auto;\n            width: calc(100% / 8);\n        }\n    }\n\n    @media (max-width: 2400px) {\n        .card-media {\n            flex: 0 0 auto;\n            width: calc(100% / 7);\n        }\n    }\n\n    @media (max-width: 2150px) {\n        .card-media {\n            flex: 0 0 auto;\n            width: calc(100% / 6);\n        }\n    }\n\n    @media (max-width: 1820px) {\n        .card-media {\n            flex: 0 0 auto;\n            width: calc(100% / 5);\n        }\n    }\n\n    @media (max-width: 1550px) {\n        .card-media {\n            flex: 1 0 auto;\n            width: calc(100% / 4);\n        }\n    }\n\n    @media (max-width: 1250px) {\n        .card-media {\n            flex: 1 0 auto;\n            width: calc(100% / 3);\n        }\n    }\n\n    @media (max-width: 950px) {\n        .card-media {\n            flex: 1 0 auto;\n            width: calc(100% / 2);\n        }\n    }\n\n    @media (max-width: 700px) {\n        .card-media {\n            flex: 0 0 auto;\n            width: 100%;\n        }\n    }\n</style>"],"sourceRoot":""}]);
