___CSS_LOADER_EXPORT___.push([module.id, "\n\n/* .test1{\n     border-left: 6px solid red !important;\n} */\n.VueTables__sort-icon[data-v-4d33e109]{\n    float: right;\n    font-size: 15px;\n    font-weight: 600;\n}\n.nonLogo[data-v-4d33e109]{\n    font-size: 14pt;\n}\n.card-logo[data-v-4d33e109]{\n    max-height: 30px;\n    max-width: 30px;\n}\n.dropdown-menu[data-v-4d33e109] {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    padding: 8px 0;\n}\n.dropdown-each-item[data-v-4d33e109] {\n    position: relative;\n    padding: 8px 32px 8px 12px;\n    cursor: pointer;\n    font-size: 14px;\n    line-height: 16px;\n    display: flex;\n    align-items: center;\n    color: #212121\n}\n.dropdown-each-item[data-v-4d33e109]:hover {\n    background: #EEEEEE;\n}\n.dropdown-text[data-v-4d33e109] {\n    margin-left: 8px;\n}\n.ide-logo[data-v-4d33e109] {\n    opacity: 0.3;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/appDataTable.vue"],"names":[],"mappings":";;AAqJA;;GAEG;AAEH;IACI,YAAY;IACZ,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,eAAe;AACnB;AACA;IACI,gBAAgB;IAChB,eAAe;AACnB;AACA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,0CAA0C;IAC1C,kBAAkB;IAClB,cAAc;AAClB;AACA;IACI,kBAAkB;IAClB,0BAA0B;IAC1B,eAAe;IACf,eAAe;IACf,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB;AACJ;AACA;IACI,mBAAmB;AACvB;AACA;IACI,gBAAgB;AACpB;AACA;IACI,YAAY;AAChB","sourcesContent":["\n<template>\n    <div class=\"vue-table\">\n\n        <v-client-table ref='table' :data=\"appValues\" :columns=\"appDatatableColumns\" :options=\"optionsDataTable\">      \n                <template #createdDate=\"props\">\n                <span>{{ new Intl.DateTimeFormat(\"tr-TR\",options).format(new Date(props.row.createdDate))}}</span>\n            </template>\n            <template #name=\"props\">             \n                <div  :style=\"{ display: 'flex', 'borderLeft': `6px solid ${props.row.color} `}\">\n                    <div class=\"col-4\" style='text-align:center'>\n                        <!-- <img v-if=\"!props.row.logo\" src='../../../../images/is-bankasi-logo.jpg'\n                            class=\"card-logo avatarLogo\"> -->\n                        <!-- <i v-if=\"!props.row.logoInfo\" class='mdi mdi-image-outline  nonLogo'></i> -->\n                        <img v-if=\"!props.row.logoInfo\" src=\"../../../../images/ide5-logo.svg\" class=\"ide-logo\" alt=\"\" />\n                        <img v-if=\"props.row.logoInfo\" :src='`${props.row.logoInfo.hostUrl}${props.row.logoInfo.path}`' class=\"card-logo avatarLogo\">\n                    </div>\n                    <div>{{ props.row.name }}</div>\n                </div>\n            </template>\n            <template #actions=\"props\">\n                <div v-if=\"props.row.synced\" class=\"col-lg-1 p-0 dot-icon\">\n                    <i class=\"mdi mdi-dots-vertical vertical-dot-icon\" id=\"dropdownMenuButton1\"\n                        data-bs-toggle=\"dropdown\" aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"></i>\n                    <ul class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton1\">\n                        <li class=\"dropdown-item dropdown-each-item\" href=\"#\" @click=\"openApp(props.row.ID)\">\n                            <i class=\"mdi mdi-open-in-app\"></i><span class=\"dropdown-text\">Open</span></li>\n                        <li class=\"dropdown-item dropdown-each-item\" @click=\"updateApp(props.row)\">\n                            <i class=\"mdi mdi-pencil-outline dropdown-icon\"></i><span class=\"dropdown-text\">Edit</span>\n                        </li>\n                        <li class=\"dropdown-item dropdown-each-item\" @click=\"deleteApp(props.row)\">\n                            <i class=\"mdi mdi-delete-outline dropdown-icon\"></i><span class=\"dropdown-text\">Delete</span>\n                        </li>\n                        <li class=\"dropdown-item dropdown-each-item\" @click=\"changeAppImage(props.row)\">\n                            <i class=\"mdi mdi-image-edit-outline dropdown-icon\"></i><span class=\"dropdown-text\">Change App Image</span>\n                        </li>\n                    </ul>\n                </div>\n            </template>\n        </v-client-table>\n    </div>\n</template>\n\n<script setup lang=\"ts\">import { container } from 'tsyringe';\nimport { defineAsyncComponent, ref } from 'vue';\nimport { IApplication, IApplicationDetails } from '../../../../domain/model/models';\nimport { IDialog } from '../../../../domain/presentation/IDialog';\nimport { IRouter } from '../../../../domain/presentation/IRouter';\nimport { IObjectUseCase } from '../../../../domain/useCase/IObjectUseCase';\nimport { IUseCaseExecutor } from '../../../../domain/useCase/IUseCaseExecutor';\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { IViewModel } from '../../../../domain/viewModel/IViewModel';\nimport { computed } from '@vue/reactivity';\n\nconst appDatatableColumns = ref(['name', 'description', 'createdBy', 'createdDate', 'modules', 'pages', 'templates', 'actions']);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst router = container.resolve<IRouter>(IRouter);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nlet options :any = { month:'2-digit',day:'2-digit', year:'numeric', hour: 'numeric', minute: 'numeric',second: 'numeric'};\ninterface IAppValues {\n    ID: IApplication[\"ID\"];\n    name: IApplication[\"name\"];\n    description: IApplication[\"description\"];\n    logoInfo: IApplication[\"logoInfo\"];\n    color: IApplication[\"color\"];\n    createdBy: IApplication[\"createdBy\"],\n    createdDate: IApplication[\"createDate\"],\n    modules: IApplicationDetails[\"moduleCount\"],\n    pages: IApplicationDetails[\"pageCount\"],\n    templates: IApplicationDetails[\"templateCount\"],\n    synced: IApplication[\"synced\"],\n};\n\nconst appValues = computed(() => {\n    return viewModel.apps.map<IAppValues>(app => {\n        const retVal:IAppValues = {\n            ID: app.ID,\n            name: app.name,\n            description: app.description,\n            logoInfo: app.logoInfo,\n            color: app.color,\n            createdBy: app.createdBy,\n            createdDate: app.createDate,\n            synced: app.synced,\n            modules: app.details?.moduleCount || 0,\n            pages: app.details?.pageCount || 0,\n            templates: app.details?.templateCount || 0,\n        };\n        return retVal;\n    });\n});\n\nconst optionsDataTable = ref({\n    filterable: false, // omit the `id` column,\n    perPage: 5,\n    perPageValues: [],\n    skin: 'app-table',\n    // sortIcon:{\n    //         base:'mdi',\n    //         is:'mdi mdi-unfold-less-horizontal',\n    //         up: 'mdi mdi-arrow-up',\n    //         down: 'mdi mdi-arrow-down'\n    //       },\n\n    // cellClasses:{ name: [ { class:'test1', condition: (row: { color: string;  }) => row.color } ] \n    // },\n\n    headings: {\n        name: localization.get('applicationListNameHeader'),\n        description: localization.get('applicationListDescriptionHeader'),\n        createdBy: localization.get('applicationListcreatedByHeader'),\n        createdDate: localization.get('applicationListcreatedDateHeader'),\n        modules: localization.get('applicationListmodulesHeader'),\n        pages: localization.get('applicationListpagesHeader'),\n        templates: localization.get('applicationListtemplatesHeader'),\n        actions: localization.get('applicationListactionsHeader')\n    },\n    texts: {\n        count: '',\n    },\n\n});\n\n\nfunction openApp(id:any){\n     router.push(`/studio/${viewModel.user!.selectedOrganization!.id}/${id}`);\n}\nfunction updateApp(app: IApplication) {\n\n    dialog.showDialog(defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)), { closable: true, title: \"Update Application\" }, { app });\n}\nfunction deleteApp(app: IApplication) {\n    executor.execute(async () => {\n        await objectUseCase.deleteObject(app.ID);\n    }, { loading: true });\n}\nfunction changeAppImage(app: IApplication) {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/appLogoDialog.vue`)),\n        { closable: true, title: \"Select App Image\", width: \"530px\" },\n        { app: app }\n    );\n}\n</script>\n<style scoped>\n\n/* .test1{\n     border-left: 6px solid red !important;\n} */\n\n.VueTables__sort-icon{\n    float: right;\n    font-size: 15px;\n    font-weight: 600;\n}\n.nonLogo{\n    font-size: 14pt;\n}\n.card-logo{\n    max-height: 30px;\n    max-width: 30px;\n}\n.dropdown-menu {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    padding: 8px 0;\n}\n.dropdown-each-item {\n    position: relative;\n    padding: 8px 32px 8px 12px;\n    cursor: pointer;\n    font-size: 14px;\n    line-height: 16px;\n    display: flex;\n    align-items: center;\n    color: #212121\n}\n.dropdown-each-item:hover {\n    background: #EEEEEE;\n}\n.dropdown-text {\n    margin-left: 8px;\n}\n.ide-logo {\n    opacity: 0.3;\n}\n</style>"],"sourceRoot":""}]);
