io/${viewModel.user!.selectedOrganization!.id}/${props.app.ID}`);\n}\n\nfunction updateApp() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/application/addOrUpdateApp.vue`)),\n        { closable: true, title: \"Update Application\" },\n        { app: props.app }\n    );\n}\n\nfunction deleteApp() {\n    executor.execute(\n        async () => {\n            await objectUseCase.deleteObject(props.app.ID);\n        },\n        { loading: true }\n    );\n}\nfunction changeAppImage() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../../components/dialogs/appLogoDialog.vue`)),\n        { closable: true, title: \"Select App Image\", width: \"530px\" },\n        { app: props.app }\n    );\n}\n\nfunction calculateDate(createDate: string) {\n    const date = new Date(createDate);\n    if(date) {\n        const minutes = parseInt(String(((new Date()).getTime() - date.getTime()) / (60 * 1000)), 10)\n        const hours = parseInt(String(minutes / 60), 10);\n        const days = parseInt(String(hours / 24), 10);\n        const weeks = parseInt(String(days / 7), 10);\n        const months = parseInt(String(weeks / 4), 10);\n        const years = parseInt(String(months / 12), 10);\n        if(years > 0) {\n            return years.toFixed() + ` year${years < 2 ? '' : 's'} ago`;\n        } else if(months > 0) {\n            return months.toFixed() + ` month${months < 2 ? '' : 's'} ago`;\n        } else if(weeks > 0) {\n            return weeks.toFixed() + ` week${weeks < 2 ? '' : 's'} ago`;\n        } else if(days > 0) {\n            return days.toFixed() + ` day${days < 2 ? '' : 's'} ago`;\n        } else if(hours > 0) {\n            return hours.toFixed() + ` hour${hours < 2 ? '' : 's'} ago`;\n        } else if(minutes > 0) {\n            return minutes.toFixed() + ` minute${minutes < 2 ? '' : 's'} ago`;\n        }\n    }\n    return \"few seconds ago\";\n}\n\n</script>\n\n<template>\n    <div v-if=\"!app.synced\" class=\"each-card not-synced\">\n\n    </div>\n    <div class=\"card each-card\">\n        <div class=\"card-image-container\" @click=\"app.synced && openApp()\">\n            <div v-if=\"app.synced\"  class=\"hover-item\"></div>\n            <span v-if=\"app.synced\"  class=\"hover-item-text\">OPEN APPLICATION</span>\n            <i class=\"mdi mdi-arrow-right mdi-18px hover-item-icon\"></i>\n            <!-- <img v-if=\"(app.logo || app.name == 'AllDemoApp')\" src=\"../../../../images/AllDemoApp.png\" class=\"card-logo\" />\n            <img v-else-if=\"(app.name == 'LibraryApp')\" src=\"../../../../images/LibraryApp.png\" class=\"card-logo\" />\n            <img v-else-if=\"(app.name == 'MobilAppDemo')\" src=\"../../../../images/MobilAppDemo.png\" class=\"card-logo\" /> -->\n            <img v-if=\"!app.synced\" style=\"width: 279px; height: 236px; z-index: 3\" src=\"../../../../images/skeletonLoader.gif\"/>\n            <img v-if=\"app.logoInfo && app.synced\" :src=\"`${app.logoInfo.hostUrl}${app.logoInfo.path}?c=${app.logoInfo.updateDate}`\" class=\"app-logo\" alt=\"\" />\n            <img v-if=\"!app.logoInfo\" src=\"../../../../images/ide5-logo.svg\" class=\"ide-logo\" alt=\"\" />\n        </div>\n        <div class=\"card-content-container\">\n            <div class=\"app-name ellipsis\" :title=\"app.name\">{{ app.name }}</div>\n            <div class=\"app-info-text ellipsis\" style=\"top: 37px\" :title=\"app.createdBy\">\n                Created by <span style=\"font-weight: 500; color: #616161;\">{{ app.createdBy?.split(\"@\")[0] || app.createdBy || \"\" }}</span>\n            </div>\n            <span v-if=\"app.createDate\" class=\"app-info-text\" style=\"top: 58px\">{{ calculateDate(app.createDate) }}</span>\n            <i\n                class=\"mdi mdi-dots-vertical vertical-dot-icon\"\n                id=\"dropdownMenuButton1\"\n                data-bs-toggle=\"dropdown\"\n                aria-expanded=\"false\"\n                data-bs-target=\"#dropdown-menu\"\n            ></i>\n            <ul class=\"dropdown-menu\" style=\"min-width: 92px\" aria-labelledby=\"dropdownMenuButton1\">\n                <li class=\"dropdown-item dropdown-each-item\" @click=\"updateApp()\">\n                    <i class=\"mdi mdi-pencil-outline dropdown-icon\"></i><span class=\"dropdown-text\">Edit</span>\n                </li>\n                <li class=\"dropdown-item dropdown-each-item\" @click=\"deleteApp()\">\n                    <i class=\"mdi mdi-delete-outline dropdown-icon\"></i><span class=\"dropdown-text\">Delete</span>\n                </li>\n                <li class=\"dropdown-item dropdown-each-item\" @click=\"changeAppImage()\">\n                    <i class=\"mdi mdi-image-edit-outline dropdown-icon\"></i><span class=\"dropdown-text\">Change App Image</span>\n                </li>\n            </ul>\n        </div>\n    </div>\n</template>\n\n<style>\n@import url(\"../../../../styles/IDE3.css\");\n</style>\n<style scoped>\n* {\n    font-family: \"Roboto\";\n}\n.not-synced {\n    position: absolute;\n    z-index: 2;\n    background-color: white;\n    opacity: 0.5;\n}\n.card-image-container {\n    position: relative;\n    cursor: pointer;\n    width: 278px;\n    height: 236px;\n    border-bottom: 1px solid #d5d5d5;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.hover-item {\n    position: absolute;\n    background: #212121 0% 0% no-repeat padding-box;\n    border-radius: 9px 9px 0px 0px;\n    opacity: 0;\n    width: 279px;\n    height: 236px;\n    cursor: pointer;\n    visibility: hidden;\n    z-index: 1;\n}\n\n.card-image-container:hover .hover-item {\n    visibility: visible;\n    opacity: 0.6;\n    transition: visibility 0s, opacity 0.1s ease-in-out;\n}\n\n.hover-item-text {\n    position: absolute;\n    top: calc(100% / 2);\n    left: 45px;\n    z-index: 2;\n    font: normal normal normal 18px/24px Roboto;\n    color: #ffffff;\n    visibility: hidden;\n}\n\n.hover-item-icon {\n    position: absolute;\n    top: calc(calc(100% / 2) - 2px);\n    left: 212px;\n    color: #ffffff;\n    z-index: 2;\n    visibility: hidden;\n}\n\n.card-image-container:hover .hover-item-text {\n    visibility: visible;\n}\n\n.card-image-container:hover .hover-item-icon {\n    visibility: visible;\n}\n\n.ide-logo {\n    position: absolute;\n    top: calc((100% - 95px) / 2);\n    left: calc((100% - 92px) / 2);\n    width: 92px;\n    height: 95px;\n    opacity: 0.3;\n}\n.app-logo {\n    width: 120px;\n    height: 120px;\n    object-fit: contain;\n    border-radius: 16px;\n}\n\n.card-content-container {\n    width: 278px;\n    height: 94px;\n    border-radius: 3px 3px 0px 0px;\n    position: relative;\n}\n\n.ellipsis {\n    white-space: nowrap;\n    overflow: hidden !important;\n    text-overflow: ellipsis;\n}\n\n.app-name {\n    position: absolute;\n    top: 12px;\n    left: 12px;\n    right: 12px;\n    font-size: 16px;\n    font-weight: 500;\n    font-family: \"Roboto\";\n    color: #151233;\n}\n\n.app-info-text {\n    position: absolute;\n    left: 12px;\n    right: 12px;\n    font-weight: 400;\n    font-size: 14px;\n    font-family: \"Roboto\";\n    color: #757575;\n}\n\n.vertical-dot-icon {\n    position: absolute;\n    top: 50px;\n    left: 244px;\n    color: #757575;\n    cursor: pointer;\n}\n.dropdown-menu {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    padding: 8px 0;\n}\n.dropdown-each-item {\n    position: relative;\n    padding: 8px 32px 8px 12px;\n    cursor: pointer;\n    font-size: 14px;\n    line-height: 16px;\n    display: flex;\n    align-items: center;\n    color: #212121\n}\n.dropdown-each-item:hover {\n    background: #EEEEEE;\n}\n\n.dropdown-text {\n    margin-left: 8px;\n}\n\n.dropdown-icon {\n    position: inherit;\n    font-size: 20px;\n}\n\n.block {\n    background-color: white;\n    opacity: 0.8;\n    height: 246px;\n    width: 332px;\n    position: absolute;\n    z-index: 1;\n}\n</style>\n"],"sourceRoot":""}]);
